body {
  display: flex;
  justify-content: center;
  align-items: center;
}

:root {
  --primary-color: #6a6a6a;
}

#root {
  width: 70%;
  height: 80vh;
}

.spin {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.6);
}

.container {
  width: 100%;
  height: 100%;
  padding: 40px;
  position: relative;
  border: 1px solid #dedede;
  border-radius: 15px;
  box-shadow: 0px 15px 30px 0px rgba(157, 157, 157, 0.8);
  display: flex;
  flex-direction: column;
}


.container_count {
    height: 100%;
    display: flex;
    align-items: center;
    gap: 20px;
}

.container_count p {
  margin-bottom: 0;
}

.container_count p span {
  font-weight: 600;
}

.title {
  font-weight: 600;
  font-size: 38px;
  color: var(--primary-color);
  display: flex;
  gap: 20px;
  align-items: center;
}

.input_enter_task {
  margin-bottom: 10px;
}

.container_list {
  margin-top: 20px;
  height: auto;
  max-height: 100%;
  overflow: auto;
}

.container_list::-webkit-scrollbar {
  width: 8px;
}

.container_list::-webkit-scrollbar-track {
  background-color: transparent;
}

.container_list::-webkit-scrollbar-thumb {
    background-color: #dedede;
    border-radius: 20px;
  }

.list {
  margin-top: 20px;
}

.ant-list-item-meta {
  align-items: center !important;
}

.ant-list-item-meta-content div {
  display: flex;
  align-items: center;
}

.ant-list-item-meta-avatar img {
  border-radius: 5px;
}

input[type="radio"],
input[type="checkbox"] {
  width: 20px;
  height: 20px;
  cursor: pointer;
  flex: none;
}

input[type="file"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.btn-upload {
  position: relative;
}

.mobile {
  display: none;
}

.popover_list {
  padding: 0px;
}

.popover_list li{
  list-style: none;
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: all .3s ;
}

.popover_list li:hover{
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  #root {
      width: 100%;
      height: 100%;
  }

  .container{
    padding: 20px;
  }

  .title {
      font-size: 24px;
      gap: 10px;
  }

  .desktop{
    display: none;
  }

  .mobile {
    display: block;
  }

  .ant-list-item-action {
    margin-inline-start: 0 !important;
  }
}
